import React, { useState } from "react";

import cn from "classnames";
import styles from "./FullPhoto.module.sass";
import Product from "../../components/Product";
import Icon from "../../components/Icon";
import { Link } from "react-router-dom";
import Masonry from "react-masonry-css";
import Modal from "../../components/Modal";

import AvaiblePilot from "../../components/AvailablePilot";

const breakpointCols = {
  default: 2,
  768: 1,
};

const breadcrumbs = [
  {
    title: "Home",
    url: "/",
  },
  {
    title: "Activities",
    url: "/",
  },
  {
    title: "Paragliding",
    url: "/category",
  },
  {
    title: "Search result",
  },
];

const gallery = [
  "/images/contenttry/Paragliding9.png",
  "/images/contenttry/Paragliding.png",
  "/images/contenttry/Paragliding2.png",
  "/images/contenttry/Paragliding3.png",
  "/images/contenttry/Paragliding4.png",
  "/images/contenttry/Paragliding5.png",
  "/images/contenttry/Paragliding6.png",
  "/images/contenttry/Paragliding7.png",
  "/images/contenttry/Paragliding8.png",
  
];

const options = [
  {
    title: "Superpilot",
    icon: "home",
  },
  {
    title: "Bir, Billing (Himachal Pradesh)",
    icon: "flag",
  },
];

const FullPhoto = () => {
  const [visible, setVisible] = useState(false);
 
  return (
    <>
 
      <Product
        classSection="section-mb64"
        urlHome="/"
        title="Paragliding"
        breadcrumbs={breadcrumbs}
        options={options}
      ></Product>
      
      <div className={cn("section-mb80", styles.section)}>
      
        <div className={cn("container", styles.container)}>
        <button onClick={()=>setVisible(true)}>List Pilots</button>
        <Modal outerClassName="bigmodelouter"  visible={visible} onClose={() => setVisible(false)}>
          <AvaiblePilot />
      </Modal>
          {gallery.map(
            (x, index) =>
              index === 0 && (
                <div className={styles.preview} key={index}>
                  <img src={x} alt="Nature" />
                </div>
              )
          )}
          <div className={styles.inner}>
            <Masonry
              className={styles.grid}
              columnClassName={styles.column}
              breakpointCols={breakpointCols}
            >
              {gallery.map(
                (x, index) =>
                  index > 0 && (
                    <div className={styles.preview} key={index}>
                      <img src={x} alt="Nature" />
                    </div>
                  )
              )}
            </Masonry>
          </div>
          <div className={styles.foot}>
            <Link
              to="/stays-product"
              className={cn("button-circle-stroke button-small", styles.button)}
            >
              <Icon name="close" size="24" />
            </Link>
          </div>
        </div>
      </div>
    </>
  );
};

export default FullPhoto;
