import React from "react";
import cn from "classnames";
import styles from "./StaysCheckout.module.sass";
import Control from "../../components/Control";
import ConfirmAndPay from "../../components/ConfirmAndPay";
import PriceDetails from "../../components/PriceDetails";

const breadcrumbs = [
  {
    title: "Paragliding",
    url: "/stays-product",
  },
  {
    title: "Confirm and pay",
  },
];

const items = [
  {
    title: "May 15, 2021",
    category: "Date",
    icon: "calendar",
  },
  {
    title: "May 22, 2021",
    category: "Time Slot",
    icon: "calendar",
  },
  {
    title: "2 guests",
    category: "People",
    icon: "user",
  },
];

const table = [
  {
    title: "Starting from (price per person)",
    value: "Rs. 2000",
  },
  {
    title: "Service fee",
    value: "Rs. 200",
  },
 
  {
    title: "Total ",
    value: "Rs. 200",
  },
];

const Checkout = () => {
  return (
    <div className={cn("section-mb80", styles.section)}>
      <div className={cn("container", styles.container)}>
        <Control
          className={styles.control}
          urlHome="/stays-product"
          breadcrumbs={breadcrumbs}
        />
        <div className={styles.wrapper}>
          <ConfirmAndPay
            className={styles.confirm}
            title="Your trip"
            buttonUrl="/checkout-complete"
            guests
          />
          <PriceDetails
            className={styles.price}
            more
            image="/images/contenttry/Paragliding6.png"
            title="Paragliding"
            items={items}
            table={table}
          />
        </div>
      </div>
    </div>
  );
};

export default Checkout;
