import React, { useState, useEffect } from "react";
import cn from "classnames";
import styles from "./Login.module.sass";
import Icon from "../Icon";
import Form from "../Form";
import TextInput from "../TextInput";
import Radio from "../Radio";
import { url } from "../../api";
import axios from "axios";
import { toast } from "react-toastify";
import { Plane } from "react-loader-spinner";
import OTPInput, { ResendOTP } from "otp-input-react";
import { useDispatch, useSelector } from "react-redux";
import { login, reset, sendOtp, userUpdate } from "../../features/authSlice";
import { Redirect } from "react-router-dom"; //import Redirect first

const Login = ({ onCloseInside }) => {
  const [email, setEmail] = useState("");
  const [confirmPhone, setConfirmPhone] = useState(true);
  const [otpComp, setOtpComp] = useState(false);
  const [redirctTo, setRedirctTo] = useState(false); // your state value to manipulate
  const [step, setStep] = useState(false);
  const [step2, setStep2] = useState(false);
  const [formState, setFormState] = useState({
    firstName: null,
    lastName: null,
    email: null,
    dob: null,
    gender: null,
  });
  const [OTP, setOTP] = useState("");
  const dispatch = useDispatch();
  const { user, isLoading, isError, isSuccess, message, otp, stepForm } =
    useSelector((state) => state.auth);

  useEffect(() => {
    if (isError) {
      toast.error(message);
      dispatch(reset());
    }

    if (isSuccess || user) {
      if (otp) {
        setOtpComp(true);
        toast.success(message);

        dispatch(reset());
      } else {
        toast.success(message);
        // dispatch(reset());
        if (!user.firstName || !user.lastName) {
          setOtpComp(false);
          setStep(true);
        } else {
          setRedirctTo(true);
          onCloseInside();
        }

        // ;
      }
    }

    //
  }, [user, isError, isLoading, isSuccess]);

  // change handle form

  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setFormState((values) => ({ ...values, [name]: value }));
  };

  // step 1,2,3
  const stepHandleForm = (e) => {
    e.preventDefault();
    console.log(formState);
    dispatch(userUpdate(formState));
  };

  const loginHandle = (e) => {
    e.preventDefault();
    dispatch(login({ phoneNumber: email, otp: OTP }));
  };

  const handleSubmitOtp = async (e) => {
    e.preventDefault();
    dispatch(sendOtp({ phoneNumber: email }));
  };

  const handleResendOtp = () => {
    dispatch(sendOtp({ phoneNumber: email }));
  };

  const popupModel = () => {
    if (otpComp) {
      return (
        <div>
          <div className={styles.item}>
            <div className={cn("h3", styles.title)}>Sign in</div>
            <div className={styles.info}>
              Enter the 4-digit authentication code that we have sent via
              message on the phone number +91 - 99887-76655
              <form className={styles.form} onSubmit={loginHandle}>
                <OTPInput
                  className="otpfieldwrap"
                  value={OTP}
                  onChange={setOTP}
                  autoFocus
                  OTPLength={4}
                  otpType="number"
                  disabled={false}
                />

                <ResendOTP onResendClick={() => handleResendOtp()} />
                <button className={cn("button", styles.button)}>Login</button>
              </form>
            </div>
          </div>
        </div>
      );
    } else if (step) {
      return (
        <div className={styles.customform}>
          <h1 className={styles.popheadinlog}>Setup your profile</h1>
          <p className={styles.popsubheadinlog}>
            Enter your personal information in the fields given below
          </p>
          <form onSubmit={stepHandleForm}>
            <div className="form-group">
              <label>First name</label>
              <input
                name="firstName"
                value={formState.firstName || ""}
                onChange={handleChange}
                className={styles.formtype}
                placeholder="Enter your Name"
              />
            </div>
            <div className="form-group">
              <label>Last name</label>
              <input
                name="lastName"
                value={formState.lastName || ""}
                onChange={handleChange}
                className={styles.formtype}
                placeholder="Enter your surname "
              />
            </div>

            <div className="form-group">
              <label>Email </label>
              <input
                name="email"
                value={formState.email || ""}
                onChange={handleChange}
                className={styles.formtype}
                placeholder="Enter your email address"
              />
            </div>

            <div className="form-group">
              <label>Date of birth</label>
              <input
                value={formState.dob || ""}
                onChange={handleChange}
                type="date"
                name="dob"
                className={styles.formtype}
                placeholder="Enter your surname "
              />
            </div>

            <div className="form-group">
              <label>gender</label>
              <select
                name="gender"
                className={styles.formtype}
                value={formState.gender || ""}
                onChange={handleChange}
              >
                <option>Select</option>
                <option value="Male">Male</option>
                <option value="Female">Female</option>
                <option name="Other">Other</option>
              </select>
            </div>

            <button className={styles.bluebtns}>Continue (1/3)</button>
          </form>
        </div>
      );
    } else if (step2) {
      return (
        <div className={styles.customform}>
          <h1 className={styles.popheadinlog}>Setup your profile</h1>
          <p className={styles.popsubheadinlog}>
            Enter your personal information in the fields given below
          </p>
          <div className="form-group">
            <label>First name</label>
            <input className={styles.formtype} placeholder="Enter your Name" />
          </div>

          <button className={styles.bluebtns}>Continue (1/3)</button>
        </div>
      );
    } else {
      return (
        <div>
          <div className={styles.item}>
            <div className={cn("h3", styles.title)}>Sign up on Try</div>
            <div className={styles.info}>
              Create an account to personalize your adventure
            </div>
            <div className={styles.btns}>
              <button className={cn("button", styles.button)}>
                <img src="/images/contenttry/google.png" alt="google" />
                <span>Google</span>
              </button>
              <button className={cn("button-black", styles.button)}>
                <img src="/images/contenttry/facebook.png" alt="fb" />
                <span>Facebook</span>
              </button>
            </div>
            <div className={styles.note}>Or continue with email</div>
            <Form
              className={styles.form}
              value={email}
              setValue={setEmail}
              onSubmit={handleSubmitOtp}
              placeholder="+91 Enter your mobile no"
              type="phone"
              name="phone"
              icon="arrow-next"
            />
            <p className={styles.textStyle}>
              By continuing, you agree to our{" "}
              <a className={styles.link} href="#">
                Terms of Service
              </a>{" "}
              and{" "}
              <a className={styles.link} href="#">
                Privacy Policy.
              </a>
            </p>
            <div className={styles.foot}>
              Already have an account?{" "}
              <a
                className={styles.link}
                href="/00000"
                rel="noopener noreferrer"
              >
                Login
              </a>
            </div>
          </div>
          <div className={styles.item}>
            <div className={cn("h3", styles.title)}>Sign in</div>
            <form className={styles.form}>
              <TextInput
                className={styles.field}
                name="password"
                type="password"
                placeholder="Password"
                required
                view
              />
              <button className={cn("button", styles.button)}>Login</button>
            </form>
            <div className={styles.foot}>
              <a
                className={styles.password}
                href="/#"
                rel="noopener noreferrer"
              >
                Forgot password?
              </a>
            </div>
          </div>
          <div className={styles.item}>
            <div className={cn("h3", styles.title)}>
              Lets confirm its really you
            </div>
            <div className={styles.info}>
              Help us secure your account. Please complete the verifications
              below
            </div>
            <form className={styles.form}>
              <div className={styles.variants}>
                <Radio
                  className={styles.radio}
                  name="confirm"
                  value={confirmPhone}
                  onChange={() => setConfirmPhone(true)}
                  content="Get the code by text message (SM) at +1 234 567 890"
                />
                <Radio
                  className={styles.radio}
                  name="confirm"
                  value={!confirmPhone}
                  onChange={() => setConfirmPhone(false)}
                  content="Get the code by email at tranm@gm.com"
                />
              </div>
              <button className={cn("button", styles.button)}>Continue</button>
            </form>
          </div>
          <div className={styles.item}>
            <div className={cn("h3", styles.title)}>
              Enter your security code
            </div>
            <div className={styles.info}>
              We texted your code to +1 234 567 890
            </div>
            <form className={styles.form}>
              <div className={styles.code}>
                <div className={styles.number}>
                  <input type="tel" />
                </div>
                <div className={styles.number}>
                  <input type="tel" />
                </div>
                <div className={styles.number}>
                  <input type="tel" />
                </div>
                <div className={styles.number}>
                  <input type="tel" />
                </div>
              </div>
            </form>
          </div>
        </div>
      );
    }
  };

  if (redirctTo) {
    return <Redirect to="/account-settings" />;
  } else {
    return (
      <div className={cn(styles.login)}>
        {isLoading && <Plane ariaLabel="loading-indicator" />}

        {popupModel()}
      </div>
    );
  }
};

export default Login;
