import React,{useState} from 'react'
import { wishlists } from "../../../mocks/wishlists";
import styles from "./Booking.module.sass";
import cn from "classnames";
import Dropdown from "../../../components/Dropdown";

import CardBooking from "../../../components/Browse/CardBooking";
import CardBucketlist from "../../../components/Browse/CardBucketlist";
import CardWishlist from "../../../components/Browse/CardWishlist";
import Loader from "../../../components/Loader";


const sortingOptions = ["Upcoming", "Future", "Past"];


export default function Booking() {
    const options = [];
    wishlists.map((x) => options.push(x.title));
  
    const [activeTab, setActiveTab] = useState(options[0]);
    const [sorting, setSorting] = useState(sortingOptions[0]);
  return (
    <div>
       <div className={styles.bookingsection}>
     <div className={styles.head}>
    <h1 className={cn("h2", styles.title)}>Bookings</h1>
   
    <div className={styles.line}>
      <div className={styles.nav}>
        {wishlists.map((x, index) => (
          <button
            className={cn(styles.link, {
              [styles.active]: x.title === activeTab,
            })}
            onClick={() => setActiveTab(x.title)}
            key={index}
          >
            {x.title}
          </button>
        ))}
      </div>
      <Dropdown
        className={cn("mobile-show", styles.dropdown)}
        value={activeTab}
        setValue={setActiveTab}
        options={options}
      />
      <Dropdown
        className={styles.dropdown}
        value={sorting}
        setValue={setSorting}
        options={sortingOptions}
      />
    </div>
  </div> 
  
  
  
  
  <div className={styles.wrapper}>
    {activeTab === options[0] && (
      <div className={styles.sunny}>
        <div className={styles.list}>
          {wishlists
            .find((x) => x.title === activeTab)
            .items.map((x, index) => (
              <CardBooking className={styles.card} item={x} key={index} />
            ))}
        </div>
        
      </div>



    )}
    {activeTab === options[1] && (
      <div className={styles.list}>
        {wishlists
          .find((x) => x.title === activeTab)
          .items.map((x, index) => (
            <CardBooking className={styles.flight} item={x} key={index} />
          ))}
      </div>
    )}
   
  </div>  
  </div>  
  <div className={styles.bucketlistsection}>
  <div className={styles.head}>
    <h1 className={cn("h2", styles.title)}>Bucketlist</h1>
    
    <div className={styles.line}>
      <div className={styles.nav}>
        {wishlists.map((x, index) => (
          <button
            className={cn(styles.link, {
              [styles.active]: x.title === activeTab,
            })}
            onClick={() => setActiveTab(x.title)}
            key={index}
          >
            {x.title}
          </button>
        ))}
      </div>
    
    </div>
  </div> 
  
  
  
  
  <div className={styles.wrapper}>
    {activeTab === options[0] && (
      <div className={styles.sunny}>
        <div className={styles.list}>
          {wishlists
            .find((x) => x.title === activeTab)
            .items.map((x, index) => (
              <CardBucketlist className={styles.card} item={x} key={index} />
            ))}
        </div>
     
      </div>



    )}
    {activeTab === options[1] && (
      <div className={styles.list}>
        {wishlists
          .find((x) => x.title === activeTab)
          .items.map((x, index) => (
            <CardBucketlist className={styles.flight} item={x} key={index} />
          ))}
      </div>
    )}
   
  </div>  
    </div>
    <div className={styles.wishlistsection}>
  <div className={styles.head}>
    <h1 className={cn("h2", styles.title)}>Wishlist</h1>
   
    <div className={styles.line}>
      <div className={styles.nav}>
        {wishlists.map((x, index) => (
          <button
            className={cn(styles.link, {
              [styles.active]: x.title === activeTab,
            })}
            onClick={() => setActiveTab(x.title)}
            key={index}
          >
            {x.title}
          </button>
        ))}
      </div>     
    </div>
  </div> 
  
  
  
  
  <div className={styles.wrapper}>
    {activeTab === options[0] && (
      <div className={styles.sunny}>
        <div className={styles.list}>
          {wishlists
            .find((x) => x.title === activeTab)
            .items.map((x, index) => (
              <CardWishlist className={styles.card} item={x} key={index} />
            ))}
        </div>
     
      </div>



    )}
    {activeTab === options[1] && (
      <div className={styles.list}>
        {wishlists
          .find((x) => x.title === activeTab)
          .items.map((x, index) => (
            <CardWishlist className={styles.flight} item={x} key={index} />
          ))}
      </div>
    )}
   
  </div>  
  </div>
  </div>




  )
}
