import React from 'react'
import styles from "../AvaiblePilot.module.sass";
export default function Card() {
  return (
    <li>
    <div className={styles.lispiotsinner}>
        <div className={styles.imgpiots}>
            <img src="/images/content/avatar.jpg" width="50px" />
        </div>
        <div className={styles.datapiots}>
            <h3>Zoe towne</h3>
           <p>Pilot experience : 3 years</p>
           <a href="">Click here to see more details</a>
           <ul  className={styles.metapiolt}>
               <li>
                   <span>	<img src="/images/contenttry/verify.png" alt="" /></span>Verified Pilot
               </li>
               <li>
                   <span><img src="/images/contenttry/ratingstar.png" alt="" /></span><strong>4.8</strong> (24 reviews)
               </li>
           </ul>
           
        </div>
        <div className={styles.pricepiots}>
             <h2>Rs. 2000 <span>/Person</span></h2>
        </div>
    </div>
  </li>
  )
}
