import React from "react";
import cn from "classnames";
import { Link } from "react-router-dom";
import styles from "./Card.module.sass";
import Icon from "../Icon";

const Item = ({ className, item, row, car }) => {
  return (
    <Link
      className={cn(
        className,
        styles.card,
        { [styles.row]: row },
        { [styles.car]: car }
      )}
      to={item.url}
    >
      <div className={styles.preview}>
        <img src={item.src} alt="Nature" />
      </div>
      <div className={styles.body}>
        <div className={styles.itemgrid}>
          <div className={styles.line}>
            <div className={styles.titlewraploc}>
              <div className={styles.title}>
                {item.title}
                <div className={styles.locationsec}>
                  {item.city}
                  <div>{item.location}</div>
                </div>
              </div>
            </div>
            <div className={styles.price}>
              <div className={styles.old}>{item.priceOld}</div>
              <div className={styles.actual}>{item.priceActual}</div>
            </div>
          </div>

          <div className={styles.options}></div>
          <div className={styles.foot}>
            <p>No. of operators : x no.</p>
            <p>Time : 15 mins</p>
            <p>Difficulty : I ntermediate</p>
            <p>A ge : 16 - 50 years</p>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default Item;
