import React from "react";
import cn from "classnames";
import styles from "./StaysCheckoutComplete.module.sass";
import Control from "../../components/Control";
import CheckoutSlider from "./CheckoutSlider";
import CheckoutComplete from "../../components/CheckoutComplete";
import Review from '../../components/Review'

const breadcrumbs = [
  {
    title: "Paragliding",
    url: "/stays-product",
  },
  {
    title: "Confirm and pay",
    url: "/checkout",
  },
  {
    title: "Checkout completed",
  },
];

const gallery = [
  {
    src: "/images/contenttry/Paragliding.png",
    srcSet: "/images/content/slider-pic-1@2x.jpg",
  },
  {
    src: "/images/contenttry/Paragliding5.png",
    srcSet: "/images/content/slider-pic-1@2x.jpg",
  },
  {
    src: "/images/contenttry/Paragliding4.png",
    srcSet: "/images/content/slider-pic-1@2x.jpg",
  },
];

const parameters = [
  {
    title: "1 Paragliding",
  },
  {
    title: "1 private Paragliding",
  },
];

const options = [
  {
    title: "Booking code:",
    content: "UI8_150989",
    icon: "hand-cart",
  },
  {
    title: "Date:",
    content: "30 Apr, 2021",
    icon: "calendar",
  },
  {
    title: "Total:",
    content: "$833",
    icon: "receipt",
  },
  {
    title: "Payment method:",
    content: "Credit card",
    icon: "wallet",
  },
];

const items = [
  {
    title: "Date",
    content: "May 15 - 22, 2021",
    icon: "clock",
    color: "#92A5EF",
  },
  {
    title: "Time",
    content: "12:30pm - 1:30pm",
    icon: "clock",
    color: "#92A5EF",
  },
  {
    title: "Guests",
    content: "2 guest",
  },
];

const StaysCheckoutComplete = () => {
  return (
    <div className={cn("section-mb80", styles.section)}>
      <div className={cn("container", styles.container)}>
        <Control
          className={styles.control}
          urlHome="/"
          breadcrumbs={breadcrumbs}
        />
        <div className={styles.row}>
          <div className={styles.col}>
            <CheckoutSlider className={styles.slider} gallery={gallery} />
          </div>
          <div className={styles.col}>
            <CheckoutComplete
              className={styles.complete}
              title="Paragliding"
              parameters={parameters}
              options={options}
              items={items}
            />
            
          </div>
          
        </div>
     
        <div className={styles.reviewsection}>
       
     <Review /> 
     <Review /> 
     </div>
      </div>
      
    </div>
  );
};

export default StaysCheckoutComplete;
