import React from "react";
import cn from "classnames";
import styles from "./Details.module.sass";
import Icon from "../../../../components/Icon";

const parameters = [
  {
    title: "2 guests",
    img: "/images/contenttry/location.svg",
  },
  {
    title: "1 bedroom",
    img: "/images/contenttry/starrate.svg",
  },
  {
    title: "1 private bath",
    img: "/images/contenttry/speed.svg",
  },
  {
    title: "2 guests",
    img: "/images/contenttry/time.svg",
  },
  {
    title: "1 bedroom",
    img: "/images/contenttry/calender.svg",
  },
  {
    title: "1 private bath",
    img: "/images/contenttry/users.svg",
  },
];

const options = [
  {
    title: "Transportation",
    icon: "modem",
  },
  {
    title: "1-hour flight extendable",
    icon: "toilet-paper",
  },
  {
    title: "Safety & Equipment",
    icon: "monitor",
  },
  {
    title: "Guide / Pilot",
    icon: "burger",
  },
  {
    title: "Amazing views",
    icon: "medical-case",
  },
  
];

const Details = ({ className }) => {
  return (
    <div className={cn(className, styles.details)}>
      <h4 className={cn("h4", styles.title)}>Paragliding</h4>
      <div className={styles.profile}>
        <span> Bir, Billing(Himachal Pradesh)</span>
        
 
      </div>
      <div className={styles.parameters}>
        {parameters.map((x, index) => (
          <div className={styles.parameter} key={index}>
            <div className={styles.paremterimg}><img src={x.img} /></div>           
                       {x.title}
          </div>
        ))}
      </div>
      <div className={styles.content}>
        <p>
        Paragliding is the recreational and competitive adventure sport of flying
paragliders: lightweight, free-flying, foot-launched glider aircraft with no rigid
primary structure. The pilot sits in a harness or lies supine in a cocoon-like
'speed bag' suspended below a fabric wing.
        </p>
       
      </div>
      <div className={styles.info}>What does this Activity offer?</div>
      <div className={styles.options}>
        {options.map((x, index) => (
          <div className={styles.option} key={index}>
            <Icon name={x.icon} size="24" />
            {x.title}
          </div>
        ))}
      </div>
      <button className={cn("button-stroke button-small", styles.button)}>
        More detail
      </button>
    </div>
  );
};

export default Details;
