import React, {useState} from "react";
import cn from "classnames";
import { Link } from "react-router-dom";
import styles from "./CardBooking.module.sass";
import Icon from "../../Icon";
import Modal from "../../Modal";
import ReviewComments from "../../ReviewComments";

const Item = ({ className, item, row, car }) => {
  const [visible, setVisible] = useState(false);
  return (
    <div className={styles.boxwrap}>
       <div className={styles.boxwrapinner}>
 
    <a  onClick={()=>setVisible(true)}

      className={cn(
        className,
        styles.card,
        { [styles.row]: row },
        { [styles.car]: car }
      )}
      
    >
      <div className={styles.boxheaders}>
	<div className={styles.boxmainimg}>
        <img  src={item.image} alt="Nature" />       
      </div>
      	<div className={styles.boxtitlesection}>
         <div className={styles.title}>{item.title}</div>
         <div className={styles.author}>
          <div className={styles.text}>Hosted by</div>
          <div className={styles.avatar}>
            <img src="/images/content/avatar.jpg" alt="Avatar" />
          </div>
          <div className={styles.man}>Zoe Towne</div>
        </div>

         <div className={styles.rastingsection}>
       <div className={styles.line}>
        <div className={styles.rating}>
          <Icon name="star" size="20" />
          <div className={styles.number}>4.8</div>
          <div className={styles.reviews}>(256 reviews)</div>
        </div>     
      </div>
      </div>
      </div>
</div>
 <div className={styles.boxmainsection}>
 	<div className={styles.boxdatesection}>
 		<div className={styles.boxdate}>
 	<small>Date</small>
 	<p>May 15,2022</p>
</div>
<div className={styles.boxdate}>
 	<small>Time</small>
 	<p>12:30pm - 1:30pm</p>
</div>
<div className={styles.boxdate}>
 	<small>People</small>
 	<p>2 people</p>
</div>
 	
</div>

<div className={styles.boxbookingdetail}>
	<h3>Booking details</h3>

	<ul className={styles.bookinglisting}>
		<li>
			<div className={styles.titlebook}> <img src="/images/contenttry/bookingicon/iconone.png" alt="" /><span>Booking code:</span></div>
			<div className={styles.bookdesc}><strong>UI8_150989</strong></div>
		</li>
		<li>
			<div className={styles.titlebook}><img src="/images/contenttry/bookingicon/icontwo.png"/><span>Date:</span></div>
			<div className={styles.bookdesc}><strong>30 Apr, 2022</strong></div>
		</li>
		<li>
			<div className={styles.titlebook}><img src="/images/contenttry/bookingicon/iconthree.png"/><span>Total:</span></div>
			<div className={styles.bookdesc}><strong>Rs. 4200</strong></div>
		</li>
		<li>
			<div className={styles.titlebook}><img src="/images/contenttry/bookingicon/iconfour.png"/><span>Payment method:</span></div>
			<div className={styles.bookdesc}><strong>Credit card</strong></div>
		</li>
	</ul>
	<p className={styles.warnningtext}><small><img src="/images/contenttry/bookingicon/iconfive.png"/>Free cancellation until 3:00 PM on May 15, 2021</small></p>
	</div>
</div>
    </a>
    </div>
    <Modal outerClassName="reviewmodelinner"  visible={visible} onClose={() => setVisible(false)}>
    <div className={styles.revieopinner}>
<h2>Add Activity review</h2>
<div className={styles.ratesec}>
	<div className={styles.ratetitle}><strong>Paragliding</strong></div>
	<div className={styles.satrs}>
  <a href="#">	<img src="/images/contenttry/bookingicon/starrate.png" /></a></div>
</div>
<div className={styles.commentsec}>
	<div className={styles.inputgroup}>
		<input type="text" placeholder="Share your thoughts" />
		<a href="#" className={styles.emojis}> <Icon name="smile" size="25" /></a>
		<button className={styles.sendbtn}>Post it! <Icon name="arrow-next" size="14" /></button>
	</div>
	</div>
<div className={styles.filterboxr}>
	<div className={styles.titlefilter}>
		<h3>3 comments</h3>
	</div>
	<div className={styles.sortbtn}>
		
	</div>
</div>

<div className={styles.reviewsbox}>
	<div className={styles.reviewsboxlist}>


	<ReviewComments />
	<ReviewComments />
	<ReviewComments />
	<ReviewComments />

 

 
	</div>
</div>

      </div>  
      </Modal>
    </div>
  );
};

export default Item;
