import React,{useState} from 'react'
import cn from "classnames";
import Icon from "../../components/Icon";
import styles from "./AvaiblePilot.module.sass";
import Card from './Card';
export default function AvaiblePilot() {
    const [activeIndex, setActiveIndex] = useState(0);
    const [visibleMap, setVisibleMap] = useState(false);
  
  return (
    <div  className={styles.popwrap}>
    <div  className={styles.popwrapinner}>
    <div  className={styles.btnwrappop}>
    <div  className={styles.piolttext}>
      <p  className={styles.availablepilot}>Available Pilots
        <span>lect individual pilot for individual person (1/2)</span></p> <div className={styles.location}>
          </div>
          </div>
          <div  className={styles.btpilot}>
      <button
        className={cn("button-stroke", styles.button, {
          [styles.active]: visibleMap,
        })}
        onClick={() => setVisibleMap(!visibleMap)}
      >
        <span>Advanced filter</span>
        <Icon name="plus" size="12" />
      </button>
  
      </div>
    </div>
    </div>
    <ul className={styles.lispiots}>
        <Card />
        <Card />
        <Card />
        <Card />
</ul>

<div className={styles.btnfootermodel}>
           <a className={styles.btnborderd} href="#">Go back</a>
      <a className={styles.btnfill} href="#">Select Pilot (1/2)</a>
      </div> 

  </div>
  )
}
