import React from "react";
import cn from "classnames";
import styles from "./Travel.module.sass";
import Slider from "react-slick";
import { Link } from "react-router-dom";
import Icon from "../Icon";
import Image from "../Image";

const items = [
  {
    title: "Find adventures that boosts your adrenaline",
    content:
      "CChoose from hundreds of activities and book at your own choices. Find adventure trips, outdoor activities and adventure holiday bundled packages in India that are made just as per your liking.",
    color: "#8BC5E5",
  },
  {
    title: "Travel with more confidence",
    content:
      "When youre ready to travel again, we're here to support you. Travel with Confidence policy features added health and safety procedures from the moment you arrive to the moment you leave, without taking away from having the time of your life.",
    color: "#92A5EF",
  },
  {
    title: "Pick amongst verified, and best rated operators",
    content:
      "You have the power to pick the pilot/guide as per your own preferences.",
    color: "#58C27D",
  },
  {
    title: "Book instantly and get all the safety benefits that come with TRY",
    content:
      "You are at liberty to decide what inclusions you want in your package. Youve options to add insurance, dedicated support, add-ons and avail safety benefits of TRY..",
    color: "#58C27D",
  },
];

const gallery = [
  {
    src: "/images/contenttry/waterfall.jpeg",
    srcSet: "/images/content/travel-pic-1@2x.jpg",
    images: [
      {
        src: "/images/contenttry/reviewuser.png",
        srcSet: "/images/content/appraisal-pic-1@2x.png",
        srcDark: "/images/content/appraisal-pic-dark-1.png",
        srcSetDark: "/images/content/appraisal-pic-dark-1@2x.png",
      },
      {
        src: "/images/contenttry/reviewuser.png",
        srcSet: "/images/content/appraisal-pic-1@2x.png",
        srcDark: "/images/content/appraisal-pic-dark-1.png",
        srcSetDark: "/images/content/appraisal-pic-dark-1@2x.png",
      },
      {
        src: "/images/contenttry/reviewuser.png",
        srcSet: "/images/content/appraisal-pic-1@2x.png",
        srcDark: "/images/content/appraisal-pic-dark-1.png",
        srcSetDark: "/images/content/appraisal-pic-dark-1@2x.png",
      },
    ],
  },
  {
    src: "/images/contenttry/waterfall.jpeg",
    srcSet: "/images/content/travel-pic-1@2x.jpg",
    images: [
      {
        src: "/images/contenttry/reviewuser.png",
        srcSet: "/images/content/appraisal-pic-1@2x.png",
        srcDark: "/images/content/appraisal-pic-dark-1.png",
        srcSetDark: "/images/content/appraisal-pic-dark-1@2x.png",
      },
      {
        src: "/images/contenttry/reviewuser.png",
        srcSet: "/images/content/appraisal-pic-1@2x.png",
        srcDark: "/images/content/appraisal-pic-dark-1.png",
        srcSetDark: "/images/content/appraisal-pic-dark-1@2x.png",
      },
    ],
  },
];

const SlickArrow = ({ currentSlide, slideCount, children, ...props }) => (
  <button {...props}>{children}</button>
);

const Travel = () => {
  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    fade: true,
    cssEase: "cubic-bezier(0.7, 0, 0.3, 1)",
    nextArrow: (
      <SlickArrow>
        <Icon name="arrow-next" size="14" />
      </SlickArrow>
    ),
    prevArrow: (
      <SlickArrow>
        <Icon name="arrow-prev" size="14" />
      </SlickArrow>
    ),
  };

  return (
    <div className={cn("section", styles.section)}>
      <div className={cn("container", styles.container)}>
        <div className={styles.head}>
          <h2 className={cn("h2", styles.title)}>
            Try adventures and create your own memories
          </h2>
          <div className={cn("info", styles.info)}>
            Find adventures that wish to try and indulge into an experience of a
            lifetime
          </div>
        </div>
        <div className={styles.row}>
          <div className={styles.col}>
            <div className={styles.list}>
              {items.map((x, index) => (
                <div className={styles.item} key={index}>
                  <div className={styles.number}>0{index + 1}</div>
                  <div className={styles.subtitle}>{x.title}</div>
                  <div className={styles.text}>{x.content}</div>
                </div>
              ))}
            </div>
            <Link className={cn("button", styles.button)} to="/activity">
              Start your search
            </Link>
          </div>
          <div className={styles.col}>
            <div className={styles.wrapper}>
              <Slider className="travel-slider" {...settings}>
                {gallery.map((item, index) => (
                  <div className={styles.gallery} key={index}>
                    <div className={cn("travel-bg", styles.bg)}>
                      <img
                        srcSet={`${item.srcSet} 2x`}
                        src={item.src}
                        alt="Travel"
                      />
                    </div>
                    <div className={styles.group}>
                      {item.images.map((x, index) => (
                        <div
                          className={cn("travel-preview", styles.preview)}
                          key={index}
                        >
                          <Image
                            srcSet={`${x.srcSet} 2x`}
                            srcSetDark={`${x.srcSetDark} 2x`}
                            src={x.src}
                            srcDark={x.srcDark}
                            alt="Appraisal"
                          />
                        </div>
                      ))}
                    </div>
                  </div>
                ))}
              </Slider>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Travel;
