import React from "react";
import cn from "classnames";
import styles from "./Profile.module.sass";
import Icon from "../Icon";
import Share from "../Share";

const Profile = ({
  className,
  parametersUser,
  info,
  socials,
  buttonText,
  children,
}) => {
  return (
    <div className={cn(className, styles.profile)}>
      <div className={styles.head}>{children}</div>
      <div className={styles.body}>
        <div className={styles.parameters}>
          {parametersUser.map((x, index) => (
            <div className={styles.parameter} key={index}>
              <Icon name={x.icon} size="20" />
              {x.title}
            </div>
          ))}
        </div>
        {info && <div className={styles.info}>{info}</div>}

        <div  className={styles.profilsocail}>      
            <a href="/" target="_blank"><img  src="/images/contenttry/greyfb.png" alt="Try" />
            </a><a href="/" target="_blank"><img  src="/images/contenttry/greyinsta.png" alt="Try" /></a>
            <a href="/" target="_blank"><img  src="/images/contenttry/greytwiter.png" alt="Try" /></a>
           
        </div>

        <ul className={styles.userinfolist}>
          <li>Gender <span>Male </span></li>
          <li>Date of birth <span>28/10/1998 </span></li>
          <li>Occupation <span>Employed </span></li>
          <li>Languges you speak <span>English, Hindi </span></li>
          <li>Link socail media  <span className={styles.soacilink}> 
          <a href="/" target="_blank"><img  src="/images/contenttry/profile-facebook.png" alt="Try" /></a>
          <a href="/" target="_blank"><img  src="/images/contenttry/profile-insta.png" alt="Try" /></a>
          <a href="/" target="_blank"><img  src="/images/contenttry/prfile-link.png" alt="Try" /></a>
            </span></li>
        </ul>
       
     
        <div className={styles.note}>Member since Mar 15, 2021</div>
     
      </div>
    </div>
  );
};

export default Profile;
