import React, { useEffect, useState } from "react";
import { Redirect } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import cn from "classnames";
import styles from "./Catalog.module.sass";
import Sorting from "../../../components/Sorting";
import Browse from "../../../components/Browse";
import Card from "../../../components/Card";
import Loader from "../../../components/Loader";

// data
import { browse2 } from "../../../mocks/browse";
import { stays } from "../../../mocks/stays";
import { catalogList } from "../../../mocks/catalog";

const breadcrumbs = [
  {
    title: "Home",
    url: "/",
  },
  {
    title: "Activities",
    url: "/",
  },
  {
    title: "Paragliding",
    url: "/category",
  },
  {
    title: "Search results",
  },
];

const navigation = [
  "Entire homes",
  "Cancellation flexibility",
  "Closest beach",
  "For long stays",
];

const saleOptions = ["On sales", "On delivery", "In exchange"];

const Catalog = () => {
  const [sale, setSale] = useState(saleOptions[0]);

  return (
    <div className={cn("section", styles.section)}>
      <Sorting
        className={styles.sorting}
        urlHome="/"
        breadcrumbs={breadcrumbs}
        navigation={navigation}
        title="Activities to TRY"
        sale="100+ Activities"
        details="May 1 - 3, 3 guests"
        sorting={sale}
        setSorting={setSale}
        sortingOptions={saleOptions}
      />

      <div className={styles.body}>
        <div className={cn("container", styles.container)}>
          <h4 className={cn("h4", styles.title)}>
            Select from the available activities and pilots
          </h4>
          <div className={styles.list}>
            {stays.map((x, index) => (
              <Card className={styles.card} item={x} key={index} />
            ))}
          </div>
          <div className={styles.btns}>
            <button className={cn("button-stroke", styles.button)}>
              <Loader className={styles.loader} />
              <span>View All</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Catalog;
