import React from "react";
import cn from "classnames";
import styles from "./StaysProduct.module.sass";
import Product from "../../components/Product";
import Description from "./Description";
import CommentsProduct from "../../components/CommentsProduct";
import Browse from "../../components/Browse";
import Newsletter from "../../components/Newsletter";
import Catalog from "./Catalog";
import { catalogList } from "../../mocks/catalog";
const breadcrumbs = [
  {
    title: "Home",
    url: "/",
  },
  {
    title: "Activities",
    url: "/",
  },
  {
    title: "Paragliding",
    url: "/category",
  },
  {
    title: "Search result",
  },
];

const gallery = [
  "/images/contenttry/Paragliding.png",
  "/images/contenttry/Paragliding7.png",
  "/images/contenttry/Paragliding2.png",
  "/images/contenttry/Paragliding4.png",
];

const options = [
  {
    title: "Verified Pilot",
    icon: "home",
  },
  {
    title: "Bir, Billing (Himachal Pradesh)",
    icon: "flag",
  },
];

const parametersUser = [
  {
    title: "Superhost",
    icon: "home",
  },
  {
    title: "256 reviews",
    icon: "star-outline",
  },
];

const socials = [
  {
    title: "twitter",
    url: "https://twitter.com/ui8",
  },
  {
    title: "instagram",
    url: "https://www.instagram.com/ui8net/",
  },
  {
    title: "facebook",
    url: "https://www.facebook.com/ui8.net/",
  },
];

const StaysProduct = () => {
  return (
    <>
      <Product
        classSection="section-mb64"
        urlHome="/category"
        title="Paragliding"
        breadcrumbs={breadcrumbs}
        options={options}
        gallery={gallery}
        type="stays"
      />
      <Description classSection="section" />

        <div className={styles.mapsection}>
        <div className={cn("container", styles.container)}>
          <h2>Location <small>(Exact location will be provided after booking)</small></h2>
          <iframe
                title="Map"
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d63817.0803287881!2d168.63234961382247!3d-45.04173987887954!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0xa9d51df1d7a8de5f%3A0x500ef868479a600!2z0JrRg9C40L3RgdGC0LDRg9C9LCDQndC-0LLQsNGPINCX0LXQu9Cw0L3QtNC40Y8!5e0!3m2!1sru!2sua!4v1624887132616!5m2!1sru!2sua"
                width="100%"
                height="450"
                loading="lazy"
              />
        </div>  
        
         </div>
      <CommentsProduct
        className={cn("section", styles.comment)}
        parametersUser={parametersUser}
        info="Pilot About me"
        socials={socials}
        buttonText="Contact"
      />
     <Browse
        classSection="section"
        classTitle="h2"
        title="More suggestions for paragliding"
        info=""
        items={catalogList[0].items}
      />
    
    </>
  );
};

export default StaysProduct;
